<template>
  <div class="hotsearch">
    <!-- 搜索框 -->
    <div>
      <form action="/">
        <van-search
          v-model="value"
          show-action
          :placeholder="title"
          @search="onSearch"
          @cancel="onCancel"
        />
      </form>
    </div>
    <div class="hint">热门搜索</div>
    <div class="content-box">
      <div
        class="content-item"
        v-show="hotIndex !== 0"
        v-for="(hotItem,hotIndex) in HotsearchData"
        :key="hotIndex"
      >
        <div class="item">
          <div class="img-box">
            <img :src="hotItem.vertical_cover" alt />
          </div>
          <div class="title-box">
            <div class="title">{{hotItem.title}}</div>
            <div class="type">{{hotItem.styles[0]}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Hotsearch",
  data() {
    return {
      value: "",
      HotsearchData: [],
      title: null
    };
  },
  created() {
    this.getSearchRecommend();
  },
  methods: {
    // 获取搜索热门漫画
    getSearchRecommend() {
      this.axios({
        method: "get",
        url: "SearchRecommend",
        params: {
          num: 7
        }
      })
        .then(res => {
          //  
          this.HotsearchData = res.data.data;
          this.title = res.data.data[0].title;
         //  
        })
        .catch(err => {
          
        });
    },

    // 搜索
    onSearch(val) {
      this.$router.push({
        name: "Search",
        params: {
          val
        }
      });
      this.getSearch(val);
    },
    onCancel() {
      this.listData = [];
      this.$router.push({
        name: "Home"
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.hint {
  text-align: left;
  color: #999;
  padding: 5px;
}
.content-box {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
  box-sizing: border-box;
  .content-item {
    width: calc(100% / 2);
    box-sizing: 5px;
    margin: 10px 0;
    .item {
      width: 100%;
      display: flex;
      .img-box {
        width: 100%;
        img {
          display: block;
          width: 100px;
        }
      }
      .title-box {
        width: 100%;
        overflow: hidden;
        position: relative;
        padding: 0px 5px;
        .title {
          width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          position: absolute;
          top: 10px;
        }
        .type {
          position: absolute;
          color: #999;
          bottom: 10px;
        }
      }
    }
  }
}
</style>